.wrapper{
  -webkit-transition: 0.7s;
  -o-transition: 0.7s;
  transition: 0.7s;
  z-index: 2;
}
.content__wrapper{
  width: 100%;
  position: absolute;
  margin: 150px auto;
  text-align: center;
  font-size: 34px;

}
.about__wrapper{
  width: 80%;
  position: absolute;
  margin: 150px 10%;
  text-align: center;
  font-size: 28px;
  line-height: 1.8;


}
.wrapper:after{
  content: '';
  display: block;
  clear: both;
}
.images-wrapper{
  width: 85%;
  margin: 0 7.5%;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack:center;
      justify-content:center;
  z-index: 99;
}

.rotate-content{
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  -webkit-transform-style: preserve-3d;
  -webkit-transform: translateX(-200px) translateY(0px) rotateY(50deg) scale(0.7);
  -moz-transform-style: preserve-3d;
  -moz-transform: translateX(-200px) translateY(0px) rotateY(50deg) scale(0.7);
  transform-style: preserve-3d;
  transform: translateX(-200px) translateY(0px) rotateY(50deg) scale(0.7);
}
.active{
  position: absolute;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
  -webkit-animation: showContent 0.5s ease-in 0.5s forwards;
          animation: showContent 0.5s ease-in 0.5s forwards;

}
.active:after{
  content: '';
  display: block;
  clear: both;
}
@-webkit-keyframes showContent{
  0%{  -webkit-transform: scale(0);  transform: scale(0); }
  100%{-webkit-transform: scale(1) ;transform: scale(1) ; }

}
@keyframes showContent{
  0%{  -webkit-transform: scale(0);  transform: scale(0); }
  100%{-webkit-transform: scale(1) ;transform: scale(1) ; }

}
.hidden{
  position: absolute;
  -webkit-animation: hiddeContent 0.5s  forwards;
          animation: hiddeContent 0.5s  forwards; 
}
@-webkit-keyframes hiddeContent{
  0%{-webkit-transform:scale(1);transform:scale(1) }
  100%{-webkit-transform:scale(0);transform:scale(0); } 

}
@keyframes hiddeContent{
  0%{-webkit-transform:scale(1);transform:scale(1) }
  100%{-webkit-transform:scale(0);transform:scale(0); } 

}
.sing-image_wrapper{
  width: 180px;
  margin: 20px 10px;
  position: relative;
  cursor: pointer;
}
.image{
  width: 100%;
}

.line{
  height: 3px;
  border-radius: 5px;
  background: #fff;
  margin: 10px;
  position: relative;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
 
}
.line1{
  width: 38px;
}
.line2{
  width: 25px;
  margin-left: 12px;
  background: red;
}
.line3{
  width: 33px;
}
.move-line:nth-child(1){
  -webkit-transform: rotateZ(90deg);
      -ms-transform: rotate(90deg);
          transform: rotateZ(90deg);
  position: relative;
  left: -13px;
  bottom: -11px;
}

.move-line:nth-child(3){
  margin-left: 10px;
  margin-top: -26px;
}
nav{
  width: 50%;
  right: 0;
  position: absolute;
  overflow: hidden;

  font-family: 'Arimo', sans-serif;
}
nav::after{
  content: '';
  display: block;
  clear: both;
}
.nav-links{
  text-align: center;
  padding: 0;
}
.nav-link{
  color: #fff;
  font-size: 44px;
  list-style: none;
  margin: 20px auto;
  cursor: pointer;
}
.nav-link:hover{
  color: #b9b9b9;
}
.menu{
  position: absolute;
  right: 0px;
  top: 0px;
  margin: 10px;
  height: 50px;
  cursor: pointer;
  z-index: 999;
}
.menu-wrapper{
  margin-top: 90px;
}
.open{
  -webkit-animation: openMenu 1s forwards;
          animation: openMenu 1s forwards;
}
.close{
  -webkit-transition:1s;
  -o-transition:1s;
  transition:1s;
  -webkit-animation: closeMenu 1s forwards;
          animation: closeMenu 1s forwards;
}
@-webkit-keyframes openMenu {
  from{display: none; margin-left: 100%}
    to{display: block;margin-left: 0%}
}
@keyframes openMenu {
  from{display: none; margin-left: 100%}
    to{display: block;margin-left: 0%}
}
@-webkit-keyframes closeMenu {
  from{display: block; margin-left: 0%}
    to{display: none;margin-left: 100%}
}
@keyframes closeMenu {
  from{display: block; margin-left: 0%}
    to{display: none;margin-left: 100%}
}
.full-image_wrapper{
  max-width: 65%;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  -webkit-animation: viewImage 1s forwards;
          animation: viewImage 1s forwards;
  z-index: 3;
}

@-webkit-keyframes viewImage {
  from{width: 10%}
  to{width:50%}
}

@keyframes viewImage {
  from{width: 10%}
  to{width:50%}
}
.close-full_image{
  color: #fff;
  cursor: pointer;
  position: absolute;
  top: -30px;
  right: -22px;
  font-size: 25px;
}
.full-image{
  width: 100%;
}
.next, .prev{
  cursor: pointer;
  color:#fff;
  background:rgba(0,0,0,0.5);
  width: 30px;
  padding: 10px 20px;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.next{
  right: 0;

}
.prev{
  left: 0;
}
.next:hover, .prev:hover{
  cursor: pointer;
  background:rgba(0,0,0,0.8);
}
.container{
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  -webkit-perspective: 1000px;
  perspective: 1000px;
}
.filter{
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  filter: brightness(20%);
  -webkit-filter: brightness(20%);
}

@media screen and (max-width:934px) {
  .full-image_wrapper{
    max-width: 90%;
  }
  @-webkit-keyframes viewImage {
    from{width: 10%}
    to{width:100%}
  }
  @keyframes viewImage {
    from{width: 10%}
    to{width:100%}
  }
  .rotate-content {
    -webkit-transform: translateX(-190px) translateY(0px) rotateY(45deg) scale(0.6);
    transform:  translateX(-190px) translateY(0px) rotateY(45deg) scale(0.6);
  }
}

@media screen and (max-width:800px) {
  .rotate-content {
    -webkit-transform: translateX(-135px) translateY(0pc) rotateY(45deg) scale(0.6);
    transform:  translateX(-135px) translateY(0px) rotateY(45deg) scale(0.6);
  }
}
@media screen and (max-width:722px) {
  .rotate-content {
    -webkit-transform:  translateX(-110px) translateY(0px) rotateY(45deg) scale(0.7);
    transform: translateX(-110px) translateY(0px) rotateY(45deg) scale(0.7);
  }
  .nav-link {
    font-size: 36px;
  }
}
@media screen and (max-width:520px) {
  .sing-image_wrapper {
    width: 160px;
  }
}
@media screen and (max-width:490px) {
  nav{
    width: 100%;
    overflow: unset;
  }
  .full-image_wrapper{
    max-width: 98%;
  }
  .sing-image_wrapper {
    width: 180px;
  }
  .images-wrapper {
    width: 85%;
  }
  .close-full_image{
    right: 10px;
    top: -35px;
  }
  .rotate-content {
    -webkit-transform: translateY(300px) translateZ(0px) rotateX(-45deg);
    transform: translateY(300px) translateZ(0px) rotateX(-45deg);
  }
  .content__wrapper {
    font-size: 22px;
  }
  @-webkit-keyframes openMenu {
    from{display: none; margin-top: -100%}
      to{display: block;margin-top: 0%}
  }
  @keyframes openMenu {
    from{display: none; margin-top: -100%}
      to{display: block;margin-top: 0%}
  }
  @-webkit-keyframes closeMenu {
    from{display: block; margin-top: 0%}
      to{display: none;margin-top: -100%}
  }
  @keyframes closeMenu {
    from{display: block; margin-top: 0%}
      to{display: none;margin-top: -100%}
  }
}



