body{
  background: black;
  font-family: 'Montserrat', sans-serif;
}
p{color:#fff}

.footer{
  z-index: 2;
  width: 300px;
  position: fixed;
  bottom: 0;
  left: calc(50% - 150px);
  background: #000;
  padding: 7px;
}
.copy{
  text-align: center; 
  margin: 0; 
  position: relative; 
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%)
}


