
.shadow__top, .shadow__bottom{
    width: 100%;
    height: 15px;
}
.shadow__top{
    background: -webkit-gradient( linear, left top, left bottom, from(black), to(transparent));
    background: linear-gradient( top, black, transparent);
    background: -webkit-linear-gradient( top, black, transparent);
    background: -o-linear-gradient( top, black, transparent);
    background: -moz-linear-gradient( top, black, transparent);
} 
.shadow__bottom{
    background: -webkit-gradient( linear, left bottom, left top, from(red), to(transparent));
    background: linear-gradient( bottom, red, transparent);
    background: -webkit-linear-gradient( bottom, red, transparent);
    background: -o-linear-gradient( bottom, red, transparent);
    background: -moz-linear-gradient( bottom, red, transparent);
}

.contact{
    width: 85%;
    height: 500px;
    margin: 0 auto;
    background: red;
    
    -webkit-transform-style: preserve-3d;
    -webkit-transform:rotateX(-90deg) rotateZ(0) translateY(250px) translateZ(-250px);
    -moz-transform-style: preserve-3d;
    -moz-transform:rotateX(-90deg) rotateZ(0) translateY(250px) translateZ(-250px);
    transform-style: preserve-3d;
    transform:rotateX(-90deg) rotateZ(0) translateY(250px) translateZ(-250px);
    
}

